// useEffect  useState  React.js 
import { ethers } from "ethers";
//import AddToHomeScreenIcon from '@material-ui/icons-material/AddToHomeScreen';
import AddToHomeScreenIcon from '@mui/icons-material/AddToHomeScreen';
import React, { useEffect, useState } from "react";
import "./styles/App.css";
import myNft from "./utils/MyNFT.json";
import Header from './header'
import Img from './img'
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import MTable from './MTable2';
import Icon from './icon';
import Button from './button';
import Footer from './footer';
import SelectButton from './SelectButton';
import Content from './content';
import Wallet from './wallet'
import Account from './account'

// Constants: const
const TWITTER_HANDLE = "Twitter";
const TWITTER_LINK = `https://twitter.com/${TWITTER_HANDLE}`;
const OPENSEA_LINK = "";
const TOTAL_MINT_COUNT = 50;
const App = () => {
  /*
   * 
   */
  const [currentAccount, setCurrentAccount] = useState("");
  /*currentAccount*/
  console.log("currentAccount: ", currentAccount);
  /*
   * 
   */
  const checkIfWalletIsConnected = async () => {
    const { ethereum } = window;
    if (!ethereum) {
      console.log("Make sure you have MetaMask!");
      return;
    } else {
      console.log("We have the ethereum object", ethereum);
    }
    /* 
     * 
     * 
     * accounts 
     */
    const accounts = await ethereum.request({ method: "eth_accounts" });

    if (accounts.length !== 0) {
      const account = accounts[0];
      console.log("Found an authorized account:", account);
      setCurrentAccount(account);
    } else {
      console.log("No authorized account found");
    }
  };

  /*
   * connectWallet 
   */
  const connectWallet = async () => {
    try {
      const { ethereum } = window;
      if (!ethereum) {
        alert("Get MetaMask!");
        return;
      }
      /*
       * 
       */
      const accounts = await ethereum.request({
        method: "eth_requestAccounts",
      });
      console.log("Connected", accounts[0]);
      /*
       *  currentAccount 
       */
      setCurrentAccount(accounts[0]);
    } catch (error) {
      console.log(error);
    }
  };

  // renderNotConnectedContainer 
  const renderNotConnectedContainer = () => (
    <button
      onClick={connectWallet}
      className="cta-button connect-wallet-button"
    >
      Connect to Wallet
    </button>
  );
  // App.js

  //Drawer
  const [drawerOpen, setDrawerOpen] = React.useState(false);//

  const handleDrawerToggle = () => {
    setDrawerOpen(!drawerOpen); // Drawer 
  };
  /*
   *  useEffect()
   */
  useEffect(() => {
    checkIfWalletIsConnected();
  }, []);

  return (
    <div className="App">
      <div className="container">
        <BrowserRouter basename="/">
          <Routes>
            <Route path="/" element={<>
              <Header />
              <Img />
              <div className="Table"><MTable /></div>
              <Icon />
              <SelectButton />
              <Content />
              <Button />
              <Footer />
            </>} />
            <Route path="/table" element={<MTable />} />
            <Route path="/wallet" element={<><Header /><div className="Wallet"><Wallet /></div><Footer /></>} />
            <Route path="/account" element={<><Header /><div className="Acoount"><Account /></div><Footer /></>} />
          </Routes>
        </BrowserRouter>
      </div>
    </div>
  );
};
export default App;